<template>  
  <section class="modlude">
    <el-form :inline="true" :model="formSearch" class="form_inline" label-width="90px">
      <el-form-item label="" size="small">
        <el-cascader
            ref="cascader"
            v-model="option1"
            :options="dataTree"
            :props="defaultProps2"
            @change="handleChange"
            clearable
          >
          </el-cascader>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-select v-model="formSearch.xqId" placeholder="">
          <el-option v-for="(item,index) in xqTree" :label="item.name" :value="item.id" :key="index"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" size="small" prop="typeId">
        <el-select v-model="formSearch.typeId" placeholder="">
          <el-option v-for="(item,index) in typeIds" :label="item.name" :value="item.id" :key="index"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-select v-model="formSearch.sex" placeholder="">
          <el-option label="" :value="0"></el-option>
          <el-option label="" :value="1"></el-option>
        </el-select>    
      </el-form-item>
      <el-form-item label="" size="small" prop="phone">
        <el-input v-model="formSearch.phone" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.age" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.company" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.department" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-input v-model="formSearch.identity" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" size="small">
        <el-date-picker
          v-model="formSearch.createTime"
          type="datetime"
          value-format="timestamp"
          placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button size="small" @click="getlist"> </el-button>
      </el-form-item>
    </el-form>
    <el-table :data="formData" style="width: 100%" stripe @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="" label="">
        <template slot-scope="scope">
          {{scope.row.sex==1?"":''}}
        </template>
      </el-table-column>
      <el-table-column prop="phone" label=""></el-table-column>
      <el-table-column prop="company" label=""></el-table-column>
      <el-table-column prop="department" label=""></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
      <el-table-column prop="email" label=""></el-table-column>
      <el-table-column prop="remark" label=""></el-table-column>
      <el-table-column prop="cname" label=""></el-table-column>
      <el-table-column prop="" label="" width="150">
        <template slot-scope="scope">
          {{$root.getDateArray(scope.row.gmtCreate)[9]}}
        </template>
      </el-table-column>
      <!-- <el-table-column label="" fixed="right" width=250>
				<template slot-scope="scope">
					<el-button type="warning" size="small" @click="updateShowBox(scope.row)"> </el-button>
					<el-button type="danger" size="small" @click="deleInfor(scope.row.id)"> </el-button>
				</template>
			</el-table-column> -->
    </el-table>
    <paging @changePage = handleCurrentPage :get-total='total'></paging>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialog" :close-on-click-modal="false">
      <div class="cont_box_right">
        <el-form label-position="right" :rules="rules" label-width="80px" :model="formPush" ref='addList'> 
          <el-form-item label="" size="small" prop="xqId">
            <el-select v-model="formPush.xqId" placeholder="">
              <el-option v-for="(item,index) in xqTree" :label="item.name" :value="item.id" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formPush.name"></el-input>
          </el-form-item>
          <el-form-item label="" size="small" prop="phone">
            <el-input v-model="formPush.phone" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formPush.address"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formPush.age"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formPush.company"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formPush.department"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formPush.email"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formPush.identity"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formPush.remark"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-radio v-model="formPush.sex" :label="0"></el-radio>
            <el-radio v-model="formPush.sex" :label="1"></el-radio>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="medium " @click="addDialog = false"> </el-button>
        <el-button size="medium " @click="addList('addList')"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="updateDialog" :close-on-click-modal="false">
      <div class="cont_box_right">
        <el-form label-position="right" :rules="rules" label-width="80px" :model="formUpdate" ref='formUpdate'> 
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.name"></el-input>
          </el-form-item>
          <el-form-item label="" size="small" prop="phone">
            <el-input v-model="formUpdate.phone" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.address"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.age"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.company"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.department"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.email"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.identity"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-input v-model="formUpdate.remark"></el-input>
          </el-form-item>
          <el-form-item label="" size="small">
            <el-radio v-model="formUpdate.sex" :label="0"></el-radio>
            <el-radio v-model="formUpdate.sex" :label="1"></el-radio>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="medium " @click="updateDialog = false"> </el-button>
        <el-button size="medium " @click="updateList('formUpdate')"> </el-button>
      </div>
    </el-dialog>
    </section>
</template>

<script>
import paging from "../paging"
import { getmaintenanceMan,addMaintenanceMan,updataModifyMaintenanceManById,delMaintenanceMan,xqSelectList,orgTree,pmtypePhone,getpropertytype } from '../../url/api';
export default {
  props:['typeId'],
  data(){
    return{
      typeIds:[],
      checkStrictly:false,
      bindRole:{},
      showmenuList:[],
      menuList:[],
      option1:[],
      xqTree:[],
      isAddorUpdate:1,//12
      formSearch:{
        typeId:'',
        roleType:1,
        current:1,
        size:10
      },
      formData: [],//
      deleBatch: [],//
      formUpdate:{},//
      total: 0,//
      pages:0,//
      addDialog:false,
      updateDialog:false,
      dialog:false,
      formPush:{
        roleType:1,
        sex:0
      },
      dataTree:[],
      defaultProps: {//key
        children: 'data',
        label: 'name'
      },
      defaultProps2: {
        children: 'children',
        label: 'name',
        value: 'id',
        expandTrigger: 'hover',checkStrictly: true
      },
      rules: {
        phone:[{ required: true, message: ''}],
        roleType:[{ required: true, message: '',trigger: 'blur'}],
        xqId:[{ required: true, message: '',trigger: 'blur'}],
      },
      imageUrl:''
    }
  },
  methods:{
    getGetpropertytype(){//
      getpropertytype().then(res=>{
        console.log(res)
        if(res.data.code == 200){
          this.typeIds = res.data.data
        }
      })
    },
    getlist(){
      getmaintenanceMan(this.formSearch).then((res)=>{
        console.log(res)
        if(res.data.code == 200){
          this.formData = res.data.data.records.filter((item)=>{
            return item
          })
          this.total = res.data.data.total
        }else{
          this.$message(res.data.msg);
        }
      })
    },
    getInit(){
      this.formSearch.typeId= this.typeId
      this.getlist()
      this.getGetpropertytype()
      xqSelectList({}).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.xqTree = res.data.data
        }
      })
      let org_tree={
        name:'',
        status:1
      }
      orgTree(org_tree).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.dataTree = res.data.data
        }
      })
    },
    handleChange(value){
      if(value.length!=0){
        console.log(value)
        this.formSearch.orgId=this.$refs.cascader.getCheckedNodes()[0].data.id
      }else{
        this.formSearch.orgId = ''
      }
    },
    handleChange1(value){
      if(value.length!=0){
        console.log(value)
        this.formPush.orgId=this.$refs.cascader.getCheckedNodes()[0].data.id
      }else{
        this.formPush.orgId = ''
      }
    },
    addList(addList){//node
      
      this.$refs[addList].validate((valid) => {
        if (valid) {
          let params =  {
            phone:this.formPush.phone
          }
          pmtypePhone(params).then((ress)=>{
            console.log(ress)
            if(ress.data.code == 200){
              addMaintenanceMan(this.formPush).then((res)=>{
                console.log(res)
                if(res.data.code == 200){
                  this.getlist()
                  this.$message({
                    message: '',
                    type: 'success'
                  });
                  
                }else{
                  this.$message(':'+res.data.msg)
                }
              })
            }else{
              this.$message(ress.data.msg)
            }
          })

          this.addDialog = false
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    updateList(formName){//
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let params =  {
            phone:this.formUpdate.phone
          }
          pmtypePhone(params).then((ress)=>{
            console.log(ress)
            if(ress.data.code == 200){
              if(this.formUpdate.gmtCreate){
                  delete this.formUpdate.gmtCreate
              }
              let params = this.formUpdate
              updataModifyMaintenanceManById(params).then((res)=>{
                console.log(res)
                if(res.data.code == 200){
                  this.getlist()
                  this.$message({
                    message: '',
                    type: 'success'
                  });
                  
                }else{
                  this.$message(':'+res.data.msg)
                }
              })
            }else{
              this.$message(ress.data.msg)
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
        this.updateDialog =false
      });
    },
    updateShowBox(item){//
      // this.formUpdate = JSON.parse(JSON.stringify(item));
      this.formUpdate = {
        address:item.address,
        name:item.name,
        phone:item.phone,
        age:item.age,
        company:item.company,
        department:item.department,
        email:item.email,
        id:item.id,
        identity:item.identity,
        remark:item.remark,
        roleType:item.roleType,
        sex:item.sex,
      }
      this.updateDialog = true
      console.log(this.formUpdate)
    },
    deleInfor(ids){//
      let arrId = []
      if(ids){
        arrId.push(ids)
      }else{
        if(this.deleBatch.length!=0){
          this.deleBatch.filter((item)=>{
            arrId.push(item.id)
            return item
          })
        }
      }
      this.$confirm('')
      .then(_ => {
        let params = {
          ids:arrId,
          roleType:1
        }
        delMaintenanceMan(params).then((res)=>{
          console.log(res)
          if(res.data.code == 200){
            this.$message('');
            this.getlist()
          }
        })
      })
      .catch(_ => {});
    },
    handleSelectionChange(val,self) {//
      this.deleBatch = val
      // let obj = {}
      // let aa = val.find(item=>item.id == self.id) 
      // console.log(aa)
      // if(aa){
      //   this.allSelect = [...this.allSelect,...val]
      //   this.allSelect = this.allSelect.reduce((cur,next) => {
      //       obj[next.id] ? "" : obj[next.id] = true && cur.push(next);
      //       return cur;
      //   },[])
      // }else{
      //   this.allSelect = this.allSelect.filter((item)=>{
      //     return item.id!=self.id
      //   })
      // }
      console.log(this.deleBatch)
      // this.deleBatch = this.allSelect
      // console.log(this.deleBatch)
      this.$emit('pushSelect',val)
    },
    handleCurrentPage(val){//
      this.formSearch.current=val
      this.getlist()
    },
  },
  mounted(){
    this.getInit()
  },
  components:{
    paging
  }
}
</script>
<style scoped>

</style>



